<template>
  <div class="cate">
    <!-- 面包屑导航区 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区 -->
    <el-card>
      <el-row>
        <el-col>

          <el-button type="primary" @click="addCate">添加分类</el-button>
        </el-col>
      </el-row>
      <!-- 商品分类数据 -->
      <tree-table
        class="treeTab"
        :data="cateList"
        :columns="columns"
        :expand-type="false"
        index-text="#"
        border
        :show-row-hover="false"
        show-index
        :selection-type="false"
      >
        <!-- 是否有效 -->
        <template slot="isok" slot-scope="scope">
          <i
            class="el-icon-success"
            v-if="scope.row.cat_deleted === false"
            style="color:lightgreen"
          ></i>
          <i class="el-icon-error" v-else style="color:red"></i>
        </template>
        <!-- 排序 -->
        <template slot="order" slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.cat_level === 0">一级</el-tag>
          <el-tag type="success" size="mini" v-else-if="scope.row.cat_level === 1">二级</el-tag>
          <el-tag type="warning" size="mini" v-else>三级</el-tag>
        </template>
        <!-- 操作 -->
        <template slot="opt" slot-scope="scope">
          <el-button type="primary" icon="el-icon-edit" size="mini" @click='editCatelog(scope.row.cat_id)'>编辑</el-button>
          <el-button type="danger" icon="el-icon-delete" size="mini" @click='removeCate(scope.row.cat_id)'>删除</el-button>
        </template>
      </tree-table>

      <!-- 分页区 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[3, 5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
    <!-- 添加分类的对话框 -->
    <el-dialog title="添加分类" :visible.sync="addCateVisible" width="50%" @close='addCateClose'>
      <!-- 添加分类的表单 -->
      <el-form
        :model="addCateForm"
        :rules="addCateFormRules"
        ref="addCateFormRef"
        label-width="100px"
      >
        <el-form-item label="分类名称" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="父级分类">
          <el-cascader
            expand-trigger="hover"
            v-model="selectedKeys"
            :options="ParentCateList"
            :props="cascaderProps"
            @change="ParentCateChanged"
            clearable
            
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCateVisible = false">取 消</el-button>
        <el-button type="primary" @click="addConfirm">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 修改对话框 -->
    <el-dialog
  title="修改分类"
  :visible.sync="editaddCatelog"
  width="50%"
  @close='editCateClose'
  >
  <!-- 修改用户信息区域表单 -->
   <el-form :model="editCate" ref ='editCateRef' label-width="70px"  :rules="editCateFormRules">
  <el-form-item label="分类名称" prop='cat_name'>
    <el-input v-model="editCate.cat_name" ></el-input>
  </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="editaddCatelog = false">取 消</el-button>
    <el-button type="primary" @click="editCateInfo">确 定</el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
      //商品分类数据列表
      cateList: [],
      // 商品分类参数
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5,
      },
      editCate:{
        cat_name:''
      },
      //总数据条数
      total: 0,
      addCateForm: {
        cat_name: '',
        cat_pid: 0,
        cat_level: 0,
      },
      addCateFormRules: {
        cat_name: [
          { required: true, message: '请输入分类名称', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: '分类名长度在 3 到 10 个字符',
            trigger: 'blur',
          },
        ],
      },
      editCateFormRules:{
        cat_name:[
           { required: true, message: '请输入分类名称', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: '分类名称长度在 3 到 10 个字符',
            trigger: 'blur',
          },
        ]
      },
      columns: [
        {
          label: '属性名称',
          prop: 'cat_name',
        },
        {
          label: '是否有效',
          type: 'template',
          template: 'isok', //表示当前列使用的模板名称
        },
        {
          label: '排序',
          type: 'template',
          template: 'order', //表示当前列使用的模板名称
        },
        {
          label: '操作',
          type: 'template',
          template: 'opt', //表示当前列使用的模板名称
        },
      ],
      //   控制添加分类的状态
      addCateVisible: false,
      editaddCatelog:false,
      //父级分类数据
      ParentCateList: [],
      cascaderProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
      },
      //选中父级分类的数组
      selectedKeys: [],
    }
  },
  watch: {},
  computed: {},
  methods: {
    //获取商品分类数据
    async getCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: this.queryInfo,
      })
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品分类数据失败')
      }
      //把数据列表赋值给cateList
      this.cateList = res.data.result
      this.total = res.data.total
    },
    //监听pagesize改变
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getCateList()
    },
    //监听pagenum的改变
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getCateList()
    },
    //添加分类
    addCate() {
      this.getParentCateList()
      this.addCateVisible = true
    },
    
    async getParentCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: { type: 2 },
      })
      if (res.meta.status !== 200) {
        return this.$message.error('获取父级分类列表失败')
      }
      this.ParentCateList = res.data
      console.log(this.ParentCateList)
    },
    //选择项发生变化
    ParentCateChanged() {
      console.log(this.selectedKeys)
      //如果selectedKeys数组中的长度大于0，证明选中的父级分类
      //反之，就说明没有选中任何父级分类
      if(this.selectedKeys.length > 0){
       this.addCateForm.cat_pid = this.selectedKeys[this.selectedKeys.length-1]
       this.addCateForm.cat_level = this.selectedKeys.length
       return
      }else{
        this.addCateForm.cat_pid = 0
       this.addCateForm.cat_level = 0
      }
    },
    addConfirm(){
     this.$refs.addCateFormRef.validate( async valid =>{
       if(!valid) return
         const { data:res } = await this.$http.post('categories',this.addCateForm)
         if(res.meta.status !== 201){
           return this.$message.error('添加分类失败')
         }
         this.$message.success('添加分类成功')
         this.getCateList()
         this.addCateVisible = false
     })
    },
    //监听对话框的关闭事件，重置表单数据
    addCateClose(){
      this.$refs.addCateFormRef.resetFields()
      this.selectedKeys = []
       this.addCateForm.cat_pid = 0
       this.addCateForm.cat_level = 0
    },
    //监听分类关闭修改事件
    editCateClose(){
       this.$refs.editCateRef.resetFields()
    },
    //修改分类信息
    async editCatelog(id){

       this.editaddCatelog = true
       console.log(id)
      const { data:res } = await this.$http.get('categories/'+id)
      console.log(res)
      if(res.meta.status !== 200){
          return this.$message.error('修改用户信息失败')
      }
      this.editCate = res.data
    },
    //修改分类
    editCateInfo(){
     this.$refs.editCateRef.validate( async valid =>{
            console.log(valid)
            if(!valid) return 
            //发起添加用户的网络请求
           const { data:res } = await this.$http.put('categories/'+this.editCate.cat_id,{
              cat_name:this.editCate.cat_name
              
           })
           if(res.meta.status !== 200){
              return this.$message.error('修改分类名称失败')
           }
            //关闭修改对话框
            this.editaddCatelog = false
            this.$message.success('修改分类名称成功')
             this.getCateList()//重新获取角色列表数据
        })
    },
    //删除分类
     async removeCate(id){
        //弹框询问用户是否删除数据
      const Reault = await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err => err)
        // 点击取消则返回字符串cancel
          if(Reault !== 'confirm'){
              return this.$message.info('已取消删除')
          }
          const {data:res } = await this.$http.delete('categories/' + id)
          if(res.meta.status !== 200){
               return this.$message.error('删除用户失败')
          }
          this.$message.success('删除用户成功')
          this.getCateList()
    },
  },
  created() {
    this.getCateList()
  },
  mounted() {},
}
</script>
<style lang="less" scoped>
.treeTab {
  margin-top: 15px;
}
.el-cascader {
  width: 100%;

}
</style>