<template>
  <div class="order">
    <!-- 面包屑导航区 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>订单管理</el-breadcrumb-item>
      <el-breadcrumb-item>订单列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区 -->
    <el-card>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="请输入内容" clearable>
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!-- 订单列表数据 -->
      <el-table :data="orderList" style="width: 100%" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="订单编号" prop='order_number' width='200'></el-table-column>
         <el-table-column label="订单价格" prop='order_price'></el-table-column>
          <el-table-column label="是否付款" prop='pay_status'>
              <template slot-scope="scope">
                 <el-tag type='success' v-if="scope.row.pay_status === '1'">已付款</el-tag>
                 <el-tag type='danger' v-else>未付款</el-tag>
              </template>
          </el-table-column>
           <el-table-column label="是否发货" prop='is_send'></el-table-column>
            <el-table-column label="下单时间" prop='create_time'>
                <template slot-scope="scope">
                    <div>
                        {{ scope.row.create_time }}
                    </div>
                </template>
            </el-table-column>
             <el-table-column label="操作">
                 <template slot-scope="scope">
                    <el-button type="primary" icon="el-icon-edit" size='mini' @click='showBox'></el-button>
                    <el-button type="success" icon="el-icon-location" size='mini'></el-button>

                 </template>
             </el-table-column>
      </el-table>
      <!-- 分页区域 -->
      <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[5, 10, 15]"
      :page-size="queryInfo.pagenum"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
    </el-card>
    <!-- 修改地址对话框 -->
    <el-dialog
  title="修改地址"
  :visible.sync="showAddVisible"
  width="50%"
  >
  <el-form :model="addressForm" :rules="addressFormRules" ref="addressFormRef" label-width="100px" class="demo-ruleForm">
  <el-form-item label="省市区、县" prop="address1">

   
  </el-form-item>
  <el-form-item label="详细地址" prop="address2">
    <el-input v-model="addressForm.address2"></el-input>
  </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="showAddVisible = false">取 消</el-button>
    <el-button type="primary" @click="showAddVisible = false">确 定</el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>

export default {
  components: {},
  props: {},
  data() {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10,
      },
      orderList: [],
      total: 0,
      showAddVisible:false,
      addressForm:{
         address1:[],
         address2:''
      },
      addressFormRules:{
          address1:[
              {
                  required:true,message:'请选择省市区县',trigger:'blur'
              }
          ],
          address2:[
              {
                  required:true,message:'请填写详细地址',trigger:'blur'
              }
          ]
      },
     
    }
  },
  watch: {},
  computed: {},
  methods: {
    async getOrderList() {
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInfo,
      })
      if (res.meta.status !== 200) {
        return this.$message.error('获取订单数据失败')
      }

      this.total = res.data.total
      this.orderList = res.data.goods
    },
    // 监听页码的改变
    handleSizeChange(newSize){
        this.queryInfo.pagesize = newSize
        this.getOrderList()
    },
    handleCurrentChange(newPage){
        this.queryInfo.pagenum = newPage
        this.getOrderList()
    },
    //展示修改地址对话框
    showBox(){
       this.showAddVisible = true
    }

  },
  created() {
    this.getOrderList()
  },
  mounted() {},
}
</script>
<style lang="less" scoped>
</style>